* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Bai Jamjuree, sans-serif;
  font-size: 16px;
}

body {
  height: 100vh;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.buttons > * {
  all: unset;
  width: 100%;
  text-align: center;
  color: #fff;
  border-radius: 1.5rem;
  padding: 1rem 0;
  font-weight: bold;
  display: block;
}

.buttons button:first-child {
  cursor: pointer;
  background: #26baa4;
  margin-bottom: 1.25rem;
  box-shadow: 0 3.2px #1e9482;
}

.buttons button:first-child:hover {
  background: #26baa4f2;
}

.buttons button:last-child {
  cursor: pointer;
  background: #6173ff;
  box-shadow: 0 3.2px #3d54ff;
}

.buttons button:last-child:hover {
  background: #6173ffd9;
}

.strong {
  text-align: center;
  padding: 2rem 1.625rem;
}

.strong h2 {
  color: #4c545d;
  margin-bottom: 1rem;
  font-size: clamp(1.25rem, 3vw + 1rem, 2rem);
}

.strong p {
  color: #9fabb2;
  width: min(100%, 43.75rem);
  margin-inline: auto;
  font-size: clamp(1rem, 1vw + .625rem, 1.25rem);
}

.small {
  text-align: center;
  padding: 2rem 1.625rem;
}

.small h3 {
  color: #4c545d;
  margin-bottom: 1rem;
  font-size: clamp(1.25rem, 3vw + 1rem, 1.5rem - 2px);
}

.small p {
  color: #9fabb2;
  width: min(100%, 21.25rem);
  margin-inline: auto;
  font-size: clamp(1rem, .5vw + .625rem, 1.125rem);
}

.group {
  margin: 1.25rem 0;
}

.group li {
  margin-bottom: .875rem;
}

.group li a {
  color: #4c545d;
}

aside {
  text-align: center;
  margin-top: 1.75rem;
}

aside p.attribution {
  color: #4c545d;
  margin-bottom: .625rem;
  font-size: clamp(.875rem, 1.09vw + .3rem, 1rem);
}

aside p.attribution a {
  color: #26baa4;
  font-weight: 600;
}

section.intro {
  text-align: center;
  background: url("../images/bg-header-mobile-hash.png") no-repeat;
  padding: clamp(5.625rem, 9vw + 1rem, 8rem) 1.625rem;
}

section.intro figure {
  margin-bottom: 2.5rem;
}

section.intro h1 {
  color: #4c545d;
  margin-bottom: 1rem;
  font-size: clamp(1.5rem, 3vw + 1rem, 3rem);
}

section.intro p {
  color: #9fabb2;
  width: min(100%, 43.75rem);
  margin-bottom: 2.5rem;
  background-color: #0000;
  margin-inline: auto;
  font-size: clamp(1rem, 1vw + .625rem, 1.25rem);
}

.features {
  padding: 0 1.625rem;
}

.features figure {
  width: 100%;
  text-align: center;
  margin-top: clamp(1rem, 2vw + 1rem, 4rem);
}

.features figure img {
  width: min(100%, 51.5625rem);
  height: auto;
}

.features .misc figure {
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}

.features .misc figure img {
  width: 100%;
  height: auto;
}

.logos {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin-bottom: 6.5625rem;
  display: flex;
}

.logos figure img {
  width: clamp(7.1875rem, 5vw + 1rem, 175px);
}

.do {
  margin-bottom: 5.625rem;
}

.do .w-icon {
  width: min(100%, 20.625rem);
  margin-inline: auto;
}

.do .w-icon figure img {
  height: 2.875rem;
  width: 2.875rem;
  object-fit: contain;
  margin: auto 0;
}

.do .w-icon h3 {
  margin-top: 2rem;
}

.do .w-icon p {
  margin-inline: auto;
}

.download {
  margin-bottom: 4.21875rem;
}

.download p {
  margin-bottom: 2rem;
}

footer {
  text-align: center;
  background: #f5f6f8;
  padding-top: 4.21875rem;
}

footer figure img {
  width: 2.625rem;
  height: auto;
}

footer nav.footer__links {
  padding: 1rem 0;
}

footer nav.footer__links ul li a:hover {
  color: #26baa4;
}

footer .social-icons {
  justify-content: center;
  align-items: center;
  gap: 1.75rem;
  margin-top: 2.375rem;
  margin-bottom: 0;
  display: flex;
}

footer .social-icons li a {
  font-size: 1.5rem;
}

@media only screen and (min-width: 65rem) {
  .buttons > * {
    width: 14.375rem;
    margin-inline: .5rem;
    display: inline;
  }

  section.intro {
    background: url("../images/bg-header-desktop-hash.png") no-repeat;
  }

  .misc {
    text-align: right;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 6.625rem;
    margin-top: 4rem;
    margin-bottom: 6rem;
    display: flex;
  }

  .misc figure {
    margin-top: auto;
    margin-bottom: auto;
    margin-left: -7%;
  }

  .misc .wrapper {
    margin: auto 0;
  }

  .misc .wrapper section {
    text-align: left;
  }

  .misc .wrapper section p {
    margin-inline: 0;
  }

  .do {
    justify-content: center;
    gap: 1.5rem;
    margin: auto 1.25rem;
    display: flex;
  }

  .logos {
    flex-direction: row;
    gap: 5.625rem;
    margin-top: 8.75rem;
  }

  footer {
    justify-content: center;
    align-items: center;
    gap: 7.5rem;
    padding: 0 8.75rem;
    display: flex;
  }

  footer nav {
    width: 100%;
    display: flex;
    position: relative;
  }

  footer nav .group {
    text-align: left;
    margin-right: 5.625rem;
  }

  footer nav .social-icons {
    position: absolute;
    right: 0;
  }
}

